/* General Styles */
.home {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; 
  flex-direction: column;
  background-image: url('../assets/logo.png'); /* Ensure the logo is the background image */
  background-position: center;
  background-size: 50%; /* Make the logo smaller by adjusting this percentage */
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  color: black;
  position: relative;
}

.headerContainer {
  text-align: center;
  padding: 20px;
  background: rgba(255, 255, 255, 0.8); /* Light background for better text visibility */
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  width: 100%;
}

.headerContainer h1 {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 20px;
  color: #333;
}

.headerContainer p {
  font-size: 1.2rem;
  margin-bottom: 30px;
  color: #555;
}

.headerContainer button {
  background-color: #0c96ff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  padding: 12px 24px;
  font-size: 1rem;
  margin: 10px;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.headerContainer button:hover {
  background-color: #91ceed;
  transform: scale(1.05);
}

.headerContainer button:focus {
  outline: none;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .home {
    background-size: 70%; /* Make the logo a bit larger on smaller screens if needed */
  }

  .headerContainer h1 {
    font-size: 1.8rem;
  }

  .headerContainer p {
    font-size: 1rem;
  }

  .headerContainer button {
    padding: 10px 20px;
    font-size: 0.9rem;
  }
}
