/* Footer container styles */
.footer {
  width: 100%;
  background-color: #121619;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px 10px;
  color: white;
  text-align: center;
}

/* Social media icons styling */
.socialMedia {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}

.socialMedia svg {
  color: white;
  font-size: 50px; /* Adjusted size for better proportion */
  cursor: pointer;
  transition: transform 0.3s ease, color 0.3s ease;
}

/* Hover effect for social media icons */
.socialMedia svg:hover {
  color: #ff0400; /* Red color on hover */
  transform: scale(1.1); /* Slight zoom effect */
}

/* Footer text styling */
.footer p {
  color: white;
  font-size: 14px; /* Adjusted font size for better readability */
  margin-top: 10px;
}

/* Responsive design for smaller screens */
@media (max-width: 768px) {
  .footer {
    padding: 20px 10px;
  }

  .socialMedia {
    gap: 15px;
  }

  .socialMedia svg {
    font-size: 40px; /* Slightly smaller icons for mobile */
  }

  .footer p {
    font-size: 12px; /* Adjust footer text size */
  }
}
