/* Apply box-sizing rule globally */
*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.App {
  min-height: 100vh; /* Ensures the app takes at least the full screen height */
  display: flex;
  flex-direction: column;
  background-color: #f9f9f9; /* Light background to make content more readable */
}

footer {
  margin-top: auto; /* Push the footer to the bottom if content is less */
}

/* Responsive design for different screen sizes */
@media (max-width: 768px) {
  .App {
    font-size: 14px; /* Adjust font size for smaller screens */
  }
}

@media (max-width: 480px) {
  .App {
    font-size: 12px; /* Adjust for mobile devices */
  }
}
