/* Background and Centering */
.login {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: url('../assets/background.png') no-repeat center center fixed;
    background-size: cover;
}

form {
    background: rgba(0, 0, 0, 0.7);
    padding: 40px;
    border-radius: 10px;
    width: 100%;
    max-width: 400px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    color: white;
}

h1 {
    text-align: center;
    margin-bottom: 30px;
    font-family: 'Arial', sans-serif;
    font-size: 2rem;
    font-weight: bold;
}

/* Input box styling */
.input-box {
    position: relative;
    margin-bottom: 20px;
}

.input-box input {
    width: 100%;
    padding: 10px 40px 10px 50px; /* Increased left padding for placeholder adjustment */
    background: #333;
    border: 1px solid #444;
    border-radius: 5px;
    color: #fff;
    font-size: 1rem;
    outline: none;
}

.input-box input:focus {
    border-color: #0d74f1;
}

.input-box .icon {
    position: absolute;
    top: 50%;
    left: 15px;
    transform: translateY(-50%);
    color: #aaa;
    font-size: 1.2rem;
}

/* Remember & Forgot password styling */
.remember-forgot {
    display: flex;
    justify-content: space-between;
    font-size: 0.9rem;
    color: #aaa;
    margin-bottom: 15px;
}

.remember-forgot label {
    display: flex;
    align-items: center;
}

.remember-forgot a {
    text-decoration: none;
    color: #0d74f1;
}

.remember-forgot a:hover {
    text-decoration: underline;
}

/* Buttons styling */
button {
    display: block; /* Ensures buttons are block elements for better layout */
    width: 100%; /* Full-width button */
    padding: 12px; /* Adjust padding for text fit */
    background-color: #0d74f1;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    text-align: center;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-bottom: 15px;
    overflow: hidden; /* Prevents content overflow */
    white-space: nowrap; /* Prevents text wrapping */
}

button:hover {
    background-color: #0a5bb7;
}

/* Secondary Button styling (Google login) */
.secondary-button {
    background-color: #db4437;
    margin-top: 20px; /* Adds space above the Google login button */
}

.secondary-button:hover {
    background-color: #c1351d;
}

/* Center Buttons */
button,
.secondary-button {
    margin: 0 auto; /* Center the buttons horizontally */
    display: block;
}

/* Register link styling */
.register-link {
    text-align: center;
    font-size: 0.9rem;
    color: #aaa;
}

.register-link a {
    text-decoration: none;
    color: #0d74f1;
}

.register-link a:hover {
    text-decoration: underline;
}
