.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 20px; /* Reduced padding for smaller navbar */
  background-color: black;
  color: white;
}

.leftSide .logo {
  height: 60px; /* Reduced logo size */
}

.rightSide {
  display: flex;
  align-items: center;
  gap: 15px; /* Reduced gap between links */
  margin-right: 15px;
}

.rightSide .hiddenLinks {
  display: flex;
  gap: 30px; /* Reduced gap between links */
}

.rightSide a {
  color: white;
  text-decoration: none;
  font-size: 18px; /* Reduced font size for links */
  margin-right: 10px; /* Reduced margin between links */
  transition: color 0.3s ease;
}

.rightSide a:hover {
  color: #ff0400;
}

.cartIcon {
  margin-left: 15px; /* Reduced margin */
  cursor: pointer;
  font-size: 1.3em; /* Reduced icon size */
}
